.wrapper {
  background: blur(50%);
  box-shadow: inset 0 0 50px #fff, /* inner white */ inset 20px 0 80px #f0f,
    /* inner left magenta short */ inset -20px 0 80px #0ff,
    /* inner right cyan short */ inset 20px 0 300px #f0f,
    /* inner left magenta broad */ inset -20px 0 300px #0ff,
    /* inner right cyan broad */ 0 0 50px #fff,
    /* outer white */ -10px 0 80px #f0f,
    /* outer left magenta */ 10px 0 80px #0ff; /* outer right cyan */
  padding: 3em 2em;
  border-radius: 30px;
  min-height: 600px;
  min-width: 300px;
}

h1 {
  margin-bottom: 1em;
  text-align: center;
  font-size: 2em;
  color: black;
}

.input {
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
}

input {
  font-family: "Handlee", cursive;
  width: 350px;
  border-radius: 5px 0 0 5px;
  border: none;
  font-size: 1.5em;
}

button {
  background-color: black;
  padding: 0.5em 1.5em;
  border-radius: 0 0.5em 0.5em 0;
  border: 1px solid transparent;
  font-family: "Handlee", cursive;
  font-weight: 600;
  cursor: pointer;
  color: white;
  text-transform: uppercase;
}

button:hover {
  border: 1px solid black;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  font-size: 1.5em;
  cursor: default;
  font-style: italic;
}

.crossed-line {
  text-decoration: line-through;
}
